import React, {useState} from 'react';
import axios from 'axios';

function App(){

  const [stateName, setState] = React.useState("test");

  React.useEffect(() => {
    axios.get("https://api.kanye.rest/").then((response) => {
      setState(response.data.quote)
    });
  }, []);

  return (
    <div>
      <h1>Hello World</h1>
      <h2>{stateName}</h2>
      <p>Okay so this is ver 5 of my App</p>
    </div>
  )
}

export default App;